    font-size: 100%;
    width: auto;
    border-radius: 3px;
    color: #f60;
}
/* Disabled & Hidden Styles */
[data-pagination] .disabled,
[data-pagination] [hidden],
[data-pagination] [disabled] {
  opacity: 0;
  pointer-events: none;
}
@media (min-width: 350px) {
